<template>
  <div v-for="user in users" :key="user.id">
    <div class="main__cars-item" v-if="user.id == currentId">
      <p>Name: {{ user.name }}</p>
      <p>Email: {{ user.email }}</p>
      <p>City: {{ user.address.city }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "UserItem",
  data() {
    return {
      users: null,
      currentId: this.$route.params.id,
    };
  },
  methods: {
    async getPens() {
      let pen = await fetch("https://jsonplaceholder.typicode.com/users");
      let cook = await pen.json();
      this.users = cook;
    },
  },
  created() {
    this.getPens();
  },
};
</script>

